	function resumeConfirm(content, success, cancel) {
		if(!content)
			content = "";
		$("#confirmContent").text(content);
		$('#delModal').modal("show");
		$("#confirmSuccess").click(function() {
			if(success) {
				success();
				cancel = null;
				success = null;
				$('#delModal').modal("hide");
				$("#confirmSuccess").unbind("click"); // 
			}
		});
		$("#confirmCancel").click(function() {
			if(cancel) {
				cancel();
				cancel = null;
				success = null;
				$('#delModal').modal("hide");
				$("#confirmCancel").unbind("click");
			}
		});
	}
	
	
